@import '../styles/App.scss';

.banner {
    width: 100%;
    position: relative;
  }
  .img-frame {
    width: 100%;
    padding-bottom: 66.5714%;
  }
  img {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }